import React, { useEffect, useState } from "react";
import style from "./FilterRatingsDropdown.module.css";
import RatingIndicator from "../RatingIndicator";
import RatingDescription from "../RatingDescription";

export const FilterRatingsDropdown = ({ data, handler, selectedValue }) => {
  const [isOptionsVisible, setOptionsVisible] = useState(false); //      
  const [text, setText] = useState(" "); //     ,  " "

  //     
  const handleOptionClick = (value) => {
    handler(value); //   
    setText(" "); //    
    setOptionsVisible(false); //     
  };

  //     
  const isActiveFilter = selectedValue !== " ";

  //  useEffect    text   isActiveFilter
  useEffect(() => {
    if (!isActiveFilter) {
      setText(" ");
    }
  }, [isActiveFilter]);

  return (
    <div className={style.container}>
      {/*   */}
      <div
        className={`${isActiveFilter ? style.filtered : style.header}`}
        onClick={() => {
          setOptionsVisible(!isOptionsVisible);
        }}
      >
        {text}
        <div
          className={isOptionsVisible ? style.select_up : style.select_drop}
        ></div>
      </div>
      {/*  */}
      {isOptionsVisible && (
        <div className={style.options}>
          <div
            key={`all-range-${Date.now()}`}
            className={style.all_rangelist}
            onClick={() => {
              handleOptionClick(" ");
              setText(" ");
            }}
          >
             
          </div>

          {data.map((item, index) => (
            <div
              key={`${index}-${Date.now()}`}
              className={`${style.option} ${
                selectedValue === item ? style.selected : ""
              }`}
              onClick={(event) => {
                const optionText = event.currentTarget.textContent;
                handleOptionClick(item);
                setText(optionText);
              }}
            >
              <RatingIndicator rate={item} />
              <RatingDescription rate={item} text={item} />
            </div>
          ))}
        </div>
      )}
    </div>
  );
};
