import React from "react";
import style from "./Profile.module.css";
import Specialists from "../Specialists";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

export const Profile = () => {
  const data = useSelector((state) => state.calls.calls);
  return (
    <div className={style.profile}>
      <Link to="/exit" className={style.exit} />
      <p className={style.name}> .</p>
      <div className={style.job_city}>
        <p></p>
        <div className={style.circle}></div>
        <p>-</p>
      </div>
      <div className={style.text}>
        <div className={style.number_icon}></div>
        <p>8 (800) 333-17-21</p>
      </div>
      <div className={style.text}>
        <div className={style.mail_icon}></div>
        <p>hi@skilla.ru</p>
      </div>
      <div className={style.line}></div>
      <p className={style.specialists}></p>
      {data
        .filter((el) => el.profession === "operator")
        .slice(0, 3)
        .map((el, i) => (
          <Specialists
            key={i}
            avatar={el.avatar}
            name={el.name}
            lastname={el.lastname}
          />
        ))}
      <p className={style.specialists}></p>
      {data
        .filter((el) => el.profession === "logistic")
        .slice(0, 3)
        .map((el, i) => (
          <Specialists
            key={i}
            avatar={el.avatar}
            name={el.name}
            lastname={el.lastname}
          />
        ))}
      <p className={style.specialists}></p>
      {data
        .filter((el) => el.profession === "accountant")
        .slice(0, 3)
        .map((el, i) => (
          <Specialists
            key={i}
            avatar={el.avatar}
            name={el.name}
            lastname={el.lastname}
          />
        ))}
    </div>
  );
};
