import React, { useState } from "react";
import style from "./Main.module.css";
import UserPanel from "../../component/UserPanel";
import FilterPanel from "../../component/FilterPanel";
import FilterCalendar from "../../ui/FilterCalendar";
import CallListContainer from "../../component/CallListContainer";
import CallListRenderer from "../../component/CallListRenderer";
import ButtonBalance from "../../ui/ButtonBalance";

export const Main = () => {
  const [value, setValue] = useState(""); //      Input (FindCall)
  const [selectedStartDate, setSelectedStartDate] = useState(null); //     (FilterCalendar)
  const [selectedEndDate, setSelectedEndDate] = useState(null); //     (FilterCalendar)
  const [selectedCallType, setSelectedCallType] = useState(" "); //      
  const [selectedExecutor, setSelectedExecutor] = useState(" "); //     
  const [selectedSource, setSelectedSource] = useState(" "); //     
  const [selectedRating, setSelectedRating] = useState(" "); //     

  //      Input
  const handleInputChange = (evt) => {
    setValue(evt.target.value);
    console.log(evt.target.value);
  };

  //    
  const handleDateRangeChange = (startDate, endDate) => {
    setSelectedStartDate(startDate);
    setSelectedEndDate(endDate);
  };

  //   
  const hasActiveFilters =
    value !== "" ||
    selectedStartDate !== null ||
    selectedEndDate !== null ||
    selectedCallType !== " " ||
    selectedExecutor !== " " ||
    selectedSource !== " " ||
    selectedRating !== " ";

  //         " "
  const handleResetFilters = () => {
    setValue("");
    setSelectedStartDate(null);
    setSelectedEndDate(null);
    setSelectedCallType(" ");
    setSelectedExecutor(" ");
    setSelectedSource(" ");
    setSelectedRating(" ");
  };

  return (
    <main className={style.body}>
      <UserPanel />
      <div className={style.main}>
        <div className={style.additional_filtering}>
          <ButtonBalance />
          <FilterCalendar
            handleDateRangeChange={handleDateRangeChange}
            selectedStartDate={selectedStartDate}
          />
        </div>
        <FilterPanel
          handleResetFilters={handleResetFilters}
          handleInputChange={handleInputChange}
          value={value}
          selectedCallType={selectedCallType}
          setSelectedCallType={setSelectedCallType}
          selectedExecutor={selectedExecutor}
          setSelectedExecutor={setSelectedExecutor}
          selectedSource={selectedSource}
          setSelectedSource={setSelectedSource}
          selectedRating={selectedRating}
          setSelectedRating={setSelectedRating}
          filtersActive={hasActiveFilters} //      FilterPanel
        />

        <CallListContainer>
          <CallListRenderer
            value={value}
            startDate={selectedStartDate}
            endDate={selectedEndDate}
            selectedCallType={selectedCallType}
            selectedExecutor={selectedExecutor}
            selectedSource={selectedSource}
            selectedRating={selectedRating}
          />
        </CallListContainer>
      </div>
    </main>
  );
};
