import React from "react";
import style from "./RatingDescription.module.css";

export const RatingDescription = ({ rate, text }) => {
  let callRate = style.great;
  let textRange = "";

  if (rate === "good") {
    callRate = style.great;
    textRange = "";
  } else if (rate === "bad") {
    callRate = style.bad;
    textRange = "";
  } else if (rate === "soso") {
    callRate = style.good;
    textRange = "";
  } else if (rate === "rejected") {
    callRate = style.rejected;
    textRange = "";
  } else {
    callRate = style.other_range;
    textRange = text;
  }

  return (
    <>
      <p className={callRate}>{textRange}</p>
    </>
  );
};
